<template>
  <div>
    <form>
      <div class="row">
        <div class="col-sm-4">
          <label>Blog Title</label>
        </div>
        <div class="col-sm-8">
          <input type="text" v-model.lazy="blog.title" />
        </div>
      </div>
      <div class="row">
        <div class="col-sm-4">
          <label>Image Url</label>
        </div>
        <div class="col-sm-8">
          <input type="text" v-model.lazy="blog.imgUrl" />
        </div>
      </div>
      <div class="row">
        <div class="col-sm-4">
          <label>Blog Content</label>
        </div>
        <div class="col-sm-8">
          <textarea v-model.lazy="blog.content"></textarea>
        </div>
      </div>
      <div class="row">
        <div class="col-sm-4">
          <label>Blog Category</label>
        </div>
        <div class="col-sm-8">
          <select v-model.lazy="blog.selectedCategory">
            <option v-for="(category, index) in categories" :key="index" >{{category}}</option>
          </select>
        </div>
      </div>
      <div class="row">
        <div class="col-sm-4">
          <label>Blog Author</label>
        </div>
        <div class="col-sm-8">
          <select v-model.lazy="blog.selectedAuthor">
            <option v-for="(author, index) in authors" :key="index">{{author}}</option>
          </select>
        </div>
      </div>
      <div class="row">
        <div class="col-sm-12 form-controls">
          <button @click.prevent="postBlog">Add Blog Post</button>
        </div>
        <div class="col-sm-12" v-if="success">
          <em>Blog was posted successfully !</em>
        </div>
      </div>
    </form>
    <hr>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      blog: {
        title: '',
        imgUrl: '',
        content: '',
        selectedAuthor: '',
        selectedCategory: '',
        timeStamp: new Date().getTime(),
      },
      authors: ['Vaibhav', 'Monalisa', 'Valentina', 'Angelina'],
      categories: ['Tech', 'Sport', 'Culture', 'Politics', 'Design', 'Health'],
      success: false,
    };
  },
  methods: {
    postBlog() {
      axios.post('https://happymarkup-3b57f.firebaseio.com/posts.json', this.blog)
        .then((response) => {
          console.log(this.blog);
          this.success = !this.success;
          setTimeout(() => {
            this.resetForm();
          }, 500);
        }).catch((error) => {
          console.log(error);
        });
    },

    resetForm() {
      const self = this;
      Object.keys(this.blog).forEach((key) => {
        self.blog[key] = '';
      });
      this.success = false;
    },
  },
};
</script>

